import Vue from 'vue'
import Router from 'vue-router'
import NotFound from '@/views/404'
import UserTable from '@/views/UserTable'
import AdminTable from '@/views/AdminTable'
import PlanShow from '@/views/PlanShow'
import BlogMgmt from '@/views/BlogMgmt'
import Charts from '@/views/Charts'
import BlogAdd from '@/views/BlogAdd'
//
import Login from '@/views/Login.vue'
//
import Home from '@/views/Home.vue'


Vue.use(Router);

let routes = [
	 {
      path: '/login',
      name: 'login',
      component: Login,
      hidden: true
    },
    {
      path: '/',
      name: '',
      component: Home
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-message',//class
        children: [
          {
            // /,
            path: '',
            redirect: ''
          },

       		{ path: '/userTable', component: UserTable, name: ''},
       		{ path: '/adminTable', component: AdminTable, name: ''},
       		{ 
            path: '/blogMgmt', 
            component: BlogMgmt, 
            name: '',
            children: [
               { path: '/blogMgmt/blogAdd', component: BlogAdd, name: ''},
            ],
          },
          { path: '/chartMgmt', component: Charts, name: ''},
        ]
    },
    {
        path: '/picMgmt',
        component: Home,
        name: '',
        iconCls: 'el-icon-message',//class
        children: [
            { path: '/plane', component: PlanShow, name: ''}, //router-view
          
        ]
    },
    // {
    //     path: '/',
    //     component: Home,
    //     name: '',
    //     iconCls: 'el-icon-message',//class
    //     children: [
    //         { path: '/table', component: Table, name: 'Table' },
    //     ]
    // },
];

//requirecommonJses6
export default routes;


