<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters"  @submit.native.prevent>
				<el-form-item>
					<!-- v-modelinput  v-bind  prop -->
					<el-input v-model="filters.name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="getUser"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<template>
			<el-table :data="users" highlight-current-row v-loading="loading" style="width: 100%;">
				<el-table-column type="index" width="60">
				</el-table-column>
				<el-table-column prop="nick_name" label="" width="120" sortable>
				</el-table-column>
				<el-table-column prop="sex" label="" width="100" :formatter="formatSex" sortable>
				</el-table-column>
				<el-table-column prop="phone" label="" width="120" sortable>
				</el-table-column>
				<el-table-column prop="zone" label="" min-width="100" sortable>
				</el-table-column>
				<el-table-column prop="email" label="" min-width="180" sortable>
				</el-table-column>
				<el-table-column label="" width="150">
				<template slot-scope="scope">
					<el-button type="primary" circle icon="el-icon-edit" @click="handleEdit(scope.$index, scope.row)"></el-button>
					<el-button type="danger" circle icon="el-icon-delete" @click="handleDel(scope.$index, scope.row)"></el-button>
				</template>
				</el-table-column>
			</el-table>
		</template>

		<!---->
		<el-col :span="24" class="toolbar"><!--:disabled="this.users.length===0"-->
			<el-button type="danger" size='small' @click="batchRemove" disabled></el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="pagesize" :total="total" style="float:right;">
			</el-pagination>
		</el-col>

		<!---->
		<el-dialog title="(123456)" :visible.sync="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
				<el-form-item label="" prop="name">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="nickName">
					<el-input v-model="addForm.nickName" auto-complete="off"></el-input>
				</el-form-item>
				<!-- prop  -->
				<el-form-item label="">
					<el-radio-group v-model="addForm.sex">
						<el-radio class="radio" :label="'1'"></el-radio>
						<el-radio class="radio" :label="'0'"></el-radio>
					</el-radio-group>
				</el-form-item>
				<!-- <el-form-item label="">
					<el-input-number v-model="addForm.age" :min="0" :max="200"></el-input-number>
				</el-form-item> -->
				<!-- <el-form-item label="">
					<el-date-picker type="date" placeholder="" v-model="addForm.birth"></el-date-picker>
				</el-form-item> -->
				<el-form-item label="" prop="phone">
					<el-input type="phone" v-model="addForm.phone"></el-input>
				</el-form-item>
				<el-form-item label="" prop="email">
					<el-input type="email" v-model="addForm.email"></el-input>
				</el-form-item>
				<el-form-item label="" prop="zone">
					<el-input type="zone" v-model="addForm.zone"></el-input>
				</el-form-item>
				
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false"></el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
			</div>
		</el-dialog>

		<!---->
		<el-dialog title="" :visible.sync="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item label="" prop="user_name">
					<el-input v-model="editForm.user_name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="nick_name">
					<el-input v-model="editForm.nick_name" auto-complete="off"></el-input>
				</el-form-item>
				<!-- prop  -->
				<el-form-item label="">
					<el-radio-group v-model="editForm.sex">
						<el-radio class="radio" :label="'1'"></el-radio>
						<el-radio class="radio" :label="'0'"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="" prop="phone">
					<el-input type="phone" v-model="editForm.phone"></el-input>
				</el-form-item>
				<el-form-item label="" prop="email">
					<el-input type="email" v-model="editForm.email"></el-input>
				</el-form-item>
				<el-form-item label="" prop="zone">
					<el-input type="zone" v-model="editForm.zone"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-upload
					  class="avatar-uploader"
					  v-model="editForm.avatar"
					  :action="imageServer"
					  :auto-upload="false"
					  ref="upload"
					  :multiple='multiple'
					  :limit=1
					  :on-change="onChangeAvatar"
					  :before-upload="beforeAvatarUpload">
					   <el-button slot="trigger" size="small" type="primary"></el-button>
					   <!-- <el-button style="margin-left: 10px;" size="small" type="success" @click.native="handleAvatarSuccess"></el-button> -->
  					   <div class="el-upload__tip" slot="tip">jpg/png files with a size less than 2mb</div>
					  <!-- <img v-if="imageUrl" :src="imageUrl" class="avatar">
					  <i v-else class="el-icon-plus avatar-uploader-icon"></i> -->
					</el-upload>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>

	</section>
</template>
<script>
	import { getAdminUserList } from '../utils/ajax';
	import { regist } from '../utils/ajax';
	import { modifyUser } from '../utils/ajax';
	import { removeUser } from '../utils/ajax';

	import {imageServer} from '../config/imgSrc';
	//import NProgress from 'nprogress'
	export default {
		data() {
			return {
				filters: {
					name: ''
				},
				imageUpload: false,
				imageServer:imageServer,
				multiple : false,

				loading: false,
				//
				users: [],
				//
				total: 0,
				page: 1,
				pagesize: 7,
				//radio: 1,
				//
				addFormVisible: false,//
				addLoading: false,
				addFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' },
					],
					nickName: [
						{ required: true, message: '', trigger: 'blur' },
					],
					sex: [
						{ required: true, message: '', trigger: 'blur' },
					],
					phone: [
						{ required: true, message: '', trigger: 'blur' },
					],
					email: [
						{ required: true, message: '', trigger: 'blur' },
					],
					zone: [
						{ required: true, message: '', trigger: 'blur' },
					],
				},
				//
				addForm: {
					name: '',
					nickName:'',
					sex: '1',
					zone: '',
					phone:'',
					email:'',
					userClass:'001'
				},

				editFormVisible : false,
				editLoading: false,
				editFormRules: {
					user_name: [
						{ required: true, message: '', trigger: 'blur' },
					],
					nick_name: [
						{ required: true, message: '', trigger: 'blur' },
					],
					sex: [
						{ required: true, message: '', trigger: 'blur' },
					],
					phone: [
						{ required: true, message: '', trigger: 'blur' },
					],
					email: [
						{ required: true, message: '', trigger: 'blur' },
					],
					zone: [
						{ required: true, message: '', trigger: 'blur' },
					],
				},
				//
				editForm: {
					user_name: '',
					nick_name:'',
					sex: 1,
					zone: '',
					phone:'',
					email:'',
					userClass:'002',
					avatar:''
				},
			}
		},
		methods: {
			//
			formatSex: function (row, column) {
				return row.sex == '1' ? '' : row.sex == '0' ? '' : '';
			},
			handleCurrentChange(val) {
				this.page = val;
				this.getUser();
			},
			//
			getUser: function () {
			//console.log("----------")
			//console.log(this.$cookies.keys())
			//console.log("----------")
				let para = {
					name: this.filters.name,
					page: this.page,
					pagesize: this.pagesize
				};
				//console.log(para.name)
				this.loading = true;
				//NProgress.start();
				getAdminUserList(para).then((res) => {
					var res = JSON.parse(res.data.resData);
					//console.log(res.length)
					if(res.length){
						//
						this.users = res;
						//console.log(res)
						this.total = res[0].total;
						//console.log(""+this.page+""+this.total+"");
					} else {
						this.users = [];
						this.total = 0;
					}

					//this.users = JSON.parse(res.data.resData);
					this.loading = false;
					//NProgress.done();
				});
			},
			//
			handleAdd: function () {
				//console.log("--------")
				this.addFormVisible = true;
				this.addForm = {
					name: '',
					nickName:'',
					sex: '1',
					zone: '',
					phone:'',
					email:'',
					userClass:'001'
				};
			},
			//
			handleDel: function (index, row) {
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.loading = true;
					//NProgress.start();
					let para = { id: row.user_id };
					removeUser(para).then((res) => {
						//this.listLoading = false;
						this.loading = false;
						//console.log(para)
						if(res.data.resCode == '01'){
							this.$message({
								message: res.data.resData,
								type: 'success'
							});
							this.getUser();
						} else {
							this.$message({
								message: res.data.resData,
								type: 'error'
							});
						}
						
					});
				}).catch(() => {

				});
			},
			//
			handleEdit: function (index, row) {
				//console.log(index)
				//console.log(row)
				this.editForm = Object.assign({}, row);
				this.editFormVisible = true;
				this.imageServer = this.imageServer + '/' + this.editForm.user_id;  //()
			},
			//
            httprequest() {

            },
			//
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
			                // let config = {
			                //     headers: {
			                //         'Content-Type': 'multipart/form-data'
			                //     }
			                // };
			                //console.log(this.imageServer )
							modifyUser(para).then((res) => {
								this.editLoading = false;
								if(res.resCode == '01'){
									this.$message({
										message: '',
										type: 'success'
									});
									//(userid),ajaxurlaction
									this.$refs.upload.submit();
									//console.log(r)
									this.$refs['editForm'].resetFields();
									//this.editFormVisible = false; //
									this.getUser();
								} else {
									this.$message({
										message: '',
										type: 'error'
									});
								}
								
							});
						}).catch(()=>{

						})
					}
				});

			},
			//
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					//console.log(valid)
					if (valid) {
						this.$confirm('', '', {type: 'warning'}).then(() => {
							this.addLoading = true;  //loading
				// 			//NProgress.start();
				 			let para = Object.assign({}, this.addForm);
				 			//console.log(para)
				 			regist(para).then((res) => {
				 				//console.log(res)
								this.addLoading = false;

								if(res.resCode != '01'){
									this.$message({
										message: res.resData,
										type: 'error'
									});

									return;
								}
				// 				//NProgress.done();
								if(res.resCode == '01'){
									this.$message({
										message: '',
										type: 'success'
									});

									this.$refs['addForm'].resetFields();
									this.addFormVisible = false;
					 				this.getUser();
					 			}
							});
						}).catch(() => {

						});
					}
				});
			},
			//
			batchRemove: function () {

			},

			//
			// handleAvatarSuccess(file) {
			// 	//console.log(res)
		 //        this.$refs.upload.submit();
		 //        //console.log(this.imageUrl )
		 //    },
		    beforeAvatarUpload(file) {
		        const isJPG = file.type === 'image/jpeg';
		        const isLt2M = file.size / 1024 / 1024 < 2;

		        if (!isJPG) {
		          this.$message.error('JPG!');
		        }
		        if (!isLt2M) {
		          this.$message.error('2MB!');
		        }
		        return isJPG && isLt2M;
		    },
		    onChangeAvatar(){
		    	// if(!this.imageUpload){
		    	// 	this.imageUpload = true;
		    	// } else {
		    	// 	this.$message.error('!');
		    	// }
		    }
		},
		mounted() {
			this.getUser();

		},
		beforeUpdate: function(){
		    console.log(this.$route.path)
		    
		}
	};

</script>

<style scoped>
  .toolbar {
    height: 60px;
  }
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 70px;
    height: 70px;
    line-height: 70px;
    text-align: center;
  }
  .avatar {
    width: 70px;
    height: 70px;
    display: block;
  }
</style>


